.wrapperHeader {
  height: 10vh;
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.06);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  position: relative;
  .contentHeader {
    width: 1200px;
    .logo {
      width: 150px;
      height: 50px;
    }
    .titleHeader {
      margin-top: 35px;
      font-size: 1.75rem;
    }
  }
}
